

*{
  font-family: Helvetica, Arial;;
}
body{
  padding: 60px;
}
input{
  width:100%;
  font-size: 1.3rem;
  color:grey;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-right: 10px;
  margin-bottom: 10px;
}
.card p {
  padding: 5px;
  text-align: center;
}
.card img{
    max-height: 170px;

}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}